<script>
import Countdown from '../partials/Countdown.vue';
import Face from '../partials/Face.vue';

export default {
  components: { Countdown, Face },
  mounted: function () {
    this.stick()
    this.petals()
    this.pistil()
    this.leaf()
    this.wateringCan()
    this.droplet()
    this.pot()
  },
  methods: {
    stick: function () {
      anime({
        targets: '#main #stick line',
        y2: [329.9, 145],
        duration: 1600,
        easing: 'easeInOutQuart'
      })
    },
    petals: function () {
      anime({
        targets: '#main #petals',
        scale: [0, 1],
        duration: 2000,
        delay: 1150,
        easing: 'easeInOutQuart'
      })
    },
    pistil: function () {
      anime({
        targets: '#main #pistil',
        scale: [0, 1],
        duration: 2000,
        delay: 250,
        easing: 'easeInOutQuart'
      })
    },
    leaf: function () {
      anime({
        targets: '#main #leaf',
        rotateY: ['-90deg', 0],
        duration: 2200,
        delay: 3800
      })
    },
    wateringCan: function () {
      anime({
        targets: '#watering-can',
        rotate: [3, -30],
        direction: 'alternate',
        duration: 2000,
        easing: 'easeOutBack',
        delay: 0
      })
      anime({
        targets: '#watering-can',
        duration: 1500,
        opacity: 0,
        easing: 'easeInExpo',
        delay: 3800
      })
    },
    pot: function () {
      anime({
        targets: '#main',
        bottom: ['5vh', '12vh'],
        duration: 1800,
        delay: 4200
      })
      anime({
        targets: '#halo',
        bottom: ['8vh', '15vh'],
        duration: 1800,
        delay: 4200
      })
    },
    droplet: function () {
      // Number of droplets
      let d = 7
      for (let i = 0; i < d-1; i++) {
        document
          .getElementById('droplets')
          .appendChild(
            document.getElementById('droplet')
            .cloneNode(true)
          )
      }
      if (window.innerWidth > 1000) {
        anime({
          targets: '#droplet',
          rotate: [0, 20],
          left: ['57%', '53%'],
          translateX: function (el, i) {
            return anime.random(-10*i, 0)
          },
          bottom: ['52vh', '15vh'],
          duration: 1200,
          delay: function (el, i) {
            return 500 + (200*i)
          },
          easing: 'easeInExpo'
        })
        anime({
          targets: '#droplet',
          opacity: [0, 1],
          delay: function (el, i) {
            return 1400 + (200*i)
          },
          duration: 1200,
          direction: 'alternate'
        })
      } else {
        anime({
          targets: '#droplet',
          top: ['-10vh', '78vh'],
          duration: function (el, i) {
            return (anime.random(20*i, 25*i) *25) + 200
          },
          left: function (el, i) {
            let p = anime.random(45, 55) + '%'
            console.log(p)
            return [p, p]
          },
          delay: function (el, i) {
            return (anime.random(10*i, 15*i) *25) + 200
          },
          easing: 'easeOutQuart'
        })
        anime({
          targets: '#droplet',
          opacity: [1, 0],
          delay: 3900,
          duration: 300,
        })
      }
    }
  }
}
</script>

