      stroke #3B5C7F
      stroke-width 2
      stroke-linecap round
      stroke-linejoin round
      stroke-miterlimit 10
  #droplet
    position absolute
    width 1vw
    left 56.5%
    bottom 49.5vh
    z-index -3
    transform-origin 50% 50%
    transform-box fill-box
    +below(1000px)
      width 2vw
    .st0
      opacity 0.6985
      fill #76B8FF
      fill-opacity 0.526
      enable-background new
    .st1
      opacity 0.6985
      fill #9CB3CC
      fill-opacity 0.526
      enable-background new
#halo
  position absolute
  left 50%
  transform translateX(-48.5%)
  bottom 15vh
  height 12vh

</style>



